 <pre class="ne-codeblock language-cpp" data-language="cpp" id="rj8l8"><span style="font-weight:bold;">8.4</span> vector<code>#include &lt;iostream&gt;
#include &lt;vector&gt; // vector 
using namespace std;
int main()
{
    vector&lt;int&gt; v1(4);							//4vector
    for (size_t i = 0; i &lt; v1.size(); i++)      // values  {0,1,2,3}
        v1.at(i) = i;
    auto v2 = v1;
    v2[2] = 1;
    if (!v1.empty()) {      //
        for (auto it = v1.begin(); it &lt; v1.end(); it++)
            cout &lt;&lt; *it &lt;&lt; " ";
    }
    cout &lt;&lt; endl;
    for(auto e:v2)
        cout &lt;&lt; e &lt;&lt; " ";
    cout &lt;&lt; endl;
    for (auto it = v1.rbegin(); it &lt; v1.rend(); it++) //
        cout &lt;&lt; *it &lt;&lt; " ";
    cout &lt;&lt; endl &lt;&lt; boolalpha;
    cout &lt;&lt; (v1 == v2) &lt;&lt; '\t' &lt;&lt; (v1 != v2)&lt;&lt; endl;
    cout &lt;&lt; (v1 &gt; v2) &lt;&lt; '\t' &lt;&lt; (v1 &lt; v2)&lt;&lt; endl;
}</code></pre>
