 <pre class="ne-codeblock language-cpp" data-language="cpp" id="YOftZ"><span style="font-weight:bold;">8.5</span> vector<code>#include&lt;iostream&gt;
#include&lt;vector&gt;
#include&lt;algorithm&gt;                  //for_eachcopy
#include&lt;iterator&gt;                   //ostream_iterator
using namespace std;
int main()
{
    vector&lt;char&gt; v;                  //vector
    string s = "LTSA";
    for(auto e:string("LTSA")) v.emplace_back(e);     // v.assign(s.begin(), s.end());
    cout &lt;&lt; v.size() &lt;&lt; endl;       //
    v.pop_back();
    for (auto it = v.rbegin(); it &lt; v.rend(); it++) //
        cout &lt;&lt; *it &lt;&lt; " ";
    cout &lt;&lt; endl;
    cout &lt;&lt; v.at(0) &lt;&lt;'\t'&lt;&lt;v.front()&lt;&lt;'\t'&lt;&lt;v.back()&lt;&lt; endl;
    v.emplace(v.begin()+1, 'C');    //1insert
    for_each(v.begin(), v.end(), [](auto &amp;elem) { cout &lt;&lt; elem &lt;&lt; ' '; });
    cout &lt;&lt; endl;
    v.erase(v.begin() + 2);          //2
    copy(v.begin(), v.end(), ostream_iterator&lt;char&gt;(cout, " "));
    return 0;
}</code></pre>
