 <pre class="ne-codeblock language-cpp" data-language="cpp" id="O5y2f"><span style="font-weight:bold;">8.6</span> <code>#include &lt;iostream&gt;
#include &lt;vector&gt;
using namespace std;
class Solution {
public:
    int findRepeatNumber(vector&lt;int&gt;&amp; nums) {
        vector&lt;int&gt; ret(nums.size(),0);
        for(auto e:nums)
        {
            if(ret[e]!=0)  return e;
            ret[e]++;
        }
        return 0;
    }
};

int main ()
{
    Solution s;
    size_t n;
    cin &gt;&gt; n;
    vector&lt;int&gt; nums(n);
    for (size_t i = 0;i&lt;n;++i){
        cin &gt;&gt; nums[i];
    }
    cout &lt;&lt; s.findRepeatNumber(nums) &lt;&lt; endl;
}</code></pre>
