 <pre class="ne-codeblock language-cpp" data-language="cpp" id="RYPog"><span style="font-weight:bold;">8.9</span> <code>#include &lt;iostream&gt;
#include &lt;algorithm&gt;
using namespace std;

void encode(char&amp; ch)
{
    if(ch=='Z')
        ch='a';
    else if(ch=='z')
        ch='A';
    else if(islower(ch))
        ch=char(toupper(ch)+1);
    else if(isupper(ch))
        ch=char(tolower(ch)+1);
}
int main()
{
    string s;
    getline(cin,s);
    for_each(s.begin(),s.end(),encode);
    cout&lt;&lt;s;
    return 0;
}</code></pre>
