 <pre class="ne-codeblock language-cpp" data-language="cpp" id="yL9xc"><span style="font-weight:bold;">8.10</span> <code>#include &lt;iostream&gt;
#include &lt;stack&gt;
using namespace std;
class Solution {
public:
    bool isValid(string s) {
        stack&lt;char&gt; st;
        for(auto e:s){
            if(e=='('||e=='['||e=='{')
                st.push(e);
            else if(!st.empty()&amp;&amp;abs(e-st.top())&lt;=2)	//ASCII2
                st.pop();
            else
                return false;
        }
        return st.empty();      						//
   } 
};
int main ()
{
    Solution s;
    string str;
    cin &gt;&gt; str;
    cout &lt;&lt;boolalpha&lt;&lt; s.isValid(str) &lt;&lt; endl;
}</code></pre>
