 <pre class="ne-codeblock language-cpp" data-language="cpp" id="bkm5a"><span style="font-weight:bold;">8.11</span> <code>class Solution {
public:
    vector&lt;int&gt; maxSlidingWindow(vector&lt;int&gt;&amp; nums, size_t k) {
        vector&lt;int&gt; ret;
        deque&lt;int&gt; qe;
        if(nums.empty())     return ret;
        for(size_t i=0;i&lt;nums.size();i++) {
            //&gt;
            while(!qe.empty()&amp;&amp;nums[i]&gt;=nums[qe.back()])
                qe.pop_back();
            //
            //i-qe.front()&gt;=k
            while(!qe.empty()&amp;&amp;i-qe.front()&gt;=k)
                qe.pop_front();
            qe.push_back(i);//""
            if(i+1&gt;=k)//
                ret.push_back(nums[qe.front()]);
        }
        return ret;
    }
};</code></pre>
