 <pre class="ne-codeblock language-cpp" data-language="cpp" id="HGNXE"><span style="font-weight:bold;">8.12</span> <code>class Solution {
public:
    int lengthOfLongestSubstring(string s) {
        queue&lt;char&gt;de;//
        vector&lt;bool&gt; arr(200,false);      //
        size_t maxsize=0;                       //
        for(size_t i=0;i&lt;s.size();i++){
            if(arr[s[i]]){                      //    
                maxsize=max(maxsize,de.size()); //
                while(arr[s[i]]){               //false
                    arr[de.front()]=false;      //
                    de.pop();     				//
                }
            }
            //
            de.push(s[i]);
            arr[s[i]]=true;                  	//
        }
        maxsize=max(maxsize,de.size());
        return maxsize;
    }
};</code></pre>
