 <pre class="ne-codeblock language-cpp" data-language="cpp" id="AJH9V"><span style="font-weight:bold;">8.13</span> <code>#include&lt;iostream&gt;
#include&lt;list&gt;
using namespace std;
int main(){
    int m,n;
    cin&gt;&gt;m&gt;&gt;n;
    list&lt;int&gt; ls;
    for(int i=0;i&lt;m;i++)
        ls.push_back(i+1);			//list
    auto it=ls.begin();
    int i=0;
    while(!ls.empty()){
        i = (i+1)%n;
        auto next=++it;				//
        if(!i) {
            cout&lt;&lt;*(--it)&lt;&lt;' ';		//
            ls.erase(it);			//it
        }
        it = next;					//
        if(it==ls.end())			//
            it=ls.begin();
    }
    return 0;
}</code></pre>
