 <pre class="ne-codeblock language-cpp" data-language="cpp" id="DCoCq"><span style="font-weight:bold;">8.14</span> <code>#include&lt;iostream&gt;
#include&lt;list&gt;
#include&lt;algorithm&gt;     //find_if
#include&lt;iterator&gt;      //ostream_iterator
using namespace std;

int main()
{
	list&lt;int&gt; ls[10005];
	int n,id1,id2, num;
	cin &gt;&gt; n;
	char str[100];
	while(n--){
		cin &gt;&gt; str;
        if(str[0]=='a'){
            cin &gt;&gt; id1 &gt;&gt; num;
            auto it=find_if(ls[id1].begin(),ls[id1].end(),[&amp;num](int v) {return v&gt;num;});
            ls[id1].insert(it,num);
        }else if(str[0]=='n'){
            cin &gt;&gt; id1; 
        }else if(str[0]=='m'){
			cin &gt;&gt; id1 &gt;&gt; id2;
			ls[id1].merge(ls[id2]);         //
        }else if(str[0]=='u'){
			cin &gt;&gt;id1;
			ls[id1].unique ();               //list
        }else if(str[0]=='o'){
			cin &gt;&gt; id1;
            copy(ls[id1].begin(), ls[id1].end(), ostream_iterator&lt;int&gt;(cout," "));
			cout &lt;&lt; endl;
        }
    }
    return 0;
}</code></pre>
