 <pre class="ne-codeblock language-cpp" data-language="cpp" id="NXA6n"><span style="font-weight:bold;">8.16</span> <code>#include &lt;iostream&gt;
#include &lt;map&gt;
using namespace std;
int main ()
{
    map&lt;int,int&gt; a;
    int n;
    cin&gt;&gt;n;
    for(int i=0;i&lt;n;++i){
        int val;
        cin&gt;&gt;val;
        if(a.count(val))
        {   if(a[val]&lt;4)   a[val]++; }
        else
            a[val]=1;				//a.insert(pair&lt;int,int&gt;(val,1));
    }
    int l1=0;
    for(auto rit=a.rbegin();rit!=a.rend();rit++){
        if(rit-&gt;second&gt;=2 &amp;&amp; l1&gt;0){              //2
            cout&lt;&lt;l1*rit-&gt;first&lt;&lt;endl;
            return 0;
        }else if(rit-&gt;second&gt;=4)                 //4
        {
            cout&lt;&lt;rit-&gt;first*rit-&gt;first&lt;&lt;endl;
            return 0;
        }else if(rit-&gt;second&gt;=2 &amp;&amp; l1==0){       //
            l1 = rit-&gt;first;
        }
    }
    cout&lt;&lt;0&lt;&lt;endl;                               //
    return 0;
}</code></pre>
