 <pre class="ne-codeblock language-cpp" data-language="cpp" id="wp2aF"><span style="font-weight:bold;">8.17</span> <code>class Solution {
public:
    vector&lt;int&gt; twoSum(vector&lt;int&gt;&amp; nums, int target) {
        unordered_map&lt;int,size_t&gt; m;
        for (size_t i = 0; i &lt; nums.size();++i)
            m[nums[i]] = i;						//i
        for (auto it1 = nums.begin(); it1 &lt; nums.end() - 1; it1++)
        {
            if (m.count(target - *it1))			//
            {
                int first = it1 - nums.begin();	//
                int second = m[target - *it1];	//
                if(first!=second){				//
                    return {first,second};		//
                }
            }
        }
        return vector&lt;int&gt;();//vector
    }
};</code></pre>
