 <pre class="ne-codeblock language-cpp" data-language="cpp" id="okF7N"><span style="font-weight:bold;">8.18</span> <code>class Solution {
public:
    bool containsNearbyDuplicate(vector&lt;int&gt;&amp; nums, int k) {
        unordered_set&lt;int&gt; s;
        for (int i = 0; i &lt; nums.size(); i++) {
            if (i &gt; k) {
                s.erase(nums[i - k - 1]);		//k
            }
            if (s.count(nums[i])) {				//
                return true;
            }
            s.emplace(nums[i]);					//
        }
        return false;
    }
};</code></pre>
