 <pre class="ne-codeblock language-cpp" data-language="cpp" id="rkpaa"><span style="font-weight:bold;">8.19</span> <code>class Solution {
public:
    int romanToInt(string s) {
        int sum=0;
        for(int i=0;i&lt;s.size();++i){
            if(s[i]=='I'){
                if(s[i+1]=='V'){++i;sum+=4;}
                else if(s[i+1]=='X'){++i;sum+=9;}
                else sum++;
            }
            else if(s[i]=='X'){
                if(s[i+1]=='L'){++i;sum+=40;}
                else if(s[i+1]=='C'){++i;sum+=90;}
                else sum+=10;
            }
            else if(s[i]=='C'){
                if(s[i+1]=='D'){++i;sum+=400;}
                else if(s[i+1]=='M'){++i;sum+=900;}
                else sum+=100;
            }
            else if(s[i]=='V'){sum+=5;}
            else if(s[i]=='L'){sum+=50;}
            else if(s[i]=='D'){sum+=500;}
            else if(s[i]=='M'){sum+=1000;}
        }
        return sum;
    }
};</code></pre>
 <p class="ne-p" id="u44548aaa" style="text-indent: 2em">
  <span class="ne-text">
   
