 <pre class="ne-codeblock language-cpp" data-language="cpp" id="T7yAG"><span style="font-weight:bold;">8.20</span> <code>class Solution {
public:
    int romanToInt(string s) {
        int sum=0;
        unordered_map&lt;char,int&gt; m1=\{\{'I',1},{'V',5},{'X',10},{'L',50},
        							{'C',100},{'D',500},{'M',1000}};
        unordered_map&lt;string,int&gt; m2=\{\{"IV",4},{"IX",9},{"XL",40},
        							{"XC",90},{"CD",400},{"CM",900}};
        for(int i=0;i&lt;s.size();++i){
            if(m2.count(s.substr(i,2))) sum+=m2[s.substr(i++,2)];
            else sum+=m1[s[i]];
        }
        return sum;
    }
};</code></pre>
